import React, {Component} from 'react' 
import {Link} from 'react-router-dom'


class FlipCard extends Component{
    state = {
        countLikes:0
    }

    incrementMe = () => {
        let newCount = this.state.countLikes + 1
        this.setState({
            countLikes: newCount
        })
      } 
    render() {
        return(
            <div className="flip-card card" ontouchstart="this.classList.toggle('hover');">
                            <div className="flip-card-inner">
                            <div className="flip-card-inner-front">
                            <span> <p>{this.props.elemnet.name}</p></span>
                            </div>
                            <div className="flip-card-inner-back">
                            <h5 className="flip-card-inner-back-title title">{this.props.elemnet.post.title}</h5>
                            <p className="flip-card-inner-back-text">{this.props.elemnet.post.description} </p>
                            <Link to={`/posts/${this.props.elemnet.post._id}`} ><button className='btn btn-info'> Visit</button></Link>
                            <button className='like-btn' onClick={this.incrementMe}> ❤️Likes: {this.state.countLikes} </button>
                            </div>
                            </div>
                        </div>  
        )
    }
} 

export default FlipCard; 


