import React, {Component} from 'react';
import {show,update} from './api';
import {withRouter} from 'react-router-dom';
import './PostEdit.css'

class PostEdit extends Component{
    state={
        dataForm:{
            title:'',
            description:''
        }
    }

    componentDidMount(){
        const user = this.props.user;
        const postId = this.props.match.params.id;
        show(user,postId)
        .then((response) => {
            const post = response.data.post
            this.setState({
                dataForm:post
            })
        })
        .catch(error => console.log(error))
    }

    handleChange = (event) => {
        //get the name of input
        const name = event.target.name;
        // get the value of input
        const value = event.target.value;
        const newForm = Object.assign(this.state.dataForm)
        newForm[name] = value;
        this.setState({
            dataForm:newForm
        })
    }


    handleSubmit = (event) =>{
        event.preventDefault();
        const user = this.props.user;
        const postId = this.props.match.params.id;
        const updatePost = this.state.dataForm;
        console.log(this.props)
        update(user,updatePost,postId)
        .then(() => this.props.history.push(`/posts/${postId}`))
        .catch((error) => console.log(error))
    }


    render(){
        // console.log(this.props)
        return(
        //     <form onSubmit={this.handleSubmit}>
        //         <label>Title</label>
        //         <input onChange={this.handleChange} type="text" name="title" value={this.state.dataForm.title}/>
        //         <label>Description</label>
        //         <input  onChange={this.handleChange} type="text" name="description" value={this.state.dataForm.description}/>
        //         <button type="submit">Update</button>
        // </form>


<React.Fragment >
<link href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.css" rel="stylesheet"/>
<div className='edit-post'> 
  <form onSubmit={this.handleSubmit} >
     
     <label> Title</label> 
    <textarea  rows="1" onChange={this.handleChange}  type="text" name="title" value={this.state.dataForm.title} class="form-control" placeholder="Title"> </textarea>
     {/* <input onChange={this.handleChange} type="text" name="title" value={this.state.dataForm.title}/>  */}
     <label>Description</label> 
     <textarea rows="5" onChange={this.handleChange}  type="text" name="description" value={this.state.dataForm.description} class="form-control" placeholder="What's in your heart"> </textarea>
     {/* <input className='desc-box' onChange={this.handleChange} type="text" name="description" value={this.state.dataForm.description}/> */}
     <button type="submit" className ="btn btn-outline-secondary edit-button">Edit</button>
  
 </form> 
 </div>  
</React.Fragment> 
        )
    }
}
export default withRouter(PostEdit)



